<template>
    <div>
        <div>
            <p></p>
        </div>
        <div class="columns">
            <div class="column" @click="updateOtetsukiUser('')">
                <RouterLink to ='/'>
                    <div class="box">
                        <p> 1</p>
                    </div>
                </RouterLink>
            </div>

            <div class="column" @click="updateOtetsukiUser('')">
                <RouterLink to = '/'>
                    <div class="box">
                        <p> </p>
                    </div>
                </RouterLink>
            </div>

            <div class="column" @click="updateOtetsukiUser('')">
                <RouterLink to = '/'>
                    <div class="box">
                        <p> 1</p>
                    </div>
                </RouterLink>
            </div>
            <div class="column" @click="updateOtetsukiUser('')">
                <RouterLink to = '/'>
                    <div class="box">
                        <p> </p>
                    </div>
                </RouterLink>
            </div>
        </div>
    </div>
</template>
<script>
export default {
  name: 'QuestionOtetsukiDetail',
  methods: {
    updateOtetsukiUser (value) {
      this.$store.dispatch('updateOtetsukiUser', {otetsuki_user: value})
    }
  }
}
</script>



